.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.maintable {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.dragdroptable {
  width: 80%;
  /* display: flex;   */
  justify-content: center;
  padding: 20px;
  background-color: rgb(255, 255, 255);
  border-radius: 5px;
  box-shadow: 0px 7px 6px #00000029;
  padding-left: 10%;
  padding-right: 10%;
}



.sentence1 {
  width: 110px;
  height: 30px;
  justify-content: center;
  padding: 5px;
  background-color: rgb(238, 238, 238);
  border: 0px;
  border-radius: 5px;
  box-shadow: 0px 7px 6px #00000029;
  padding-left: 20px;
  padding-right: 20px;
  margin: 8px;
}
.tblcellcolor{
  background-color: #F5D38B;
  width: 110px;
  height: 30px;
  text-align: center;
  background: #F5D38B 0% 0% no-repeat padding-box;
  box-shadow: 0px 3px 6px #00000029;
  border-radius: 5px;
  opacity: 1;
  color: #412410;
  padding-top: 5px;
  margin: 5px;
  border: 0px;

}


.btnsubmit{
  width: 130px;
  height: 35px;
  background: #FFFFFF 0% 0% no-repeat padding-box;
  border-radius: 5px;
  opacity: 1;
  color: #439803;
  border: 0px solid #202020;
  box-shadow: 0px 7px 6px #00000029;
  font-weight: bold;
  font-size: 14px;
  margin-right: 10px;
}

.btnclear{
  width: 130px;
  height: 35px;
  background: #FFFFFF 0% 0% no-repeat padding-box;
  border-radius: 5px;
  opacity: 1;
  color: #E65353;
  border: 0px solid #1b1a1a;
  box-shadow: 0px 7px 6px #00000029;
  font-weight: bold;
  font-size: 14px;
  margin-right: 10px;
}


.btnnext{
  width: 130px;
  height: 35px;
  background: #FFFFFF 0% 0% no-repeat padding-box;
  border-radius: 5px;
  opacity: 1;
  color: #F6A700;
  border: 0px solid #1b1a1a;
  box-shadow: 0px 7px 6px #00000029;
  font-weight: bold;
  font-size: 14px;
  margin-right: 10px;
}


.btnall{
  border: 0px;
  font-size: 14px;
  box-shadow: 0px 7px 6px #00000029;
  font-weight: bold;
  border-radius:5px;
  padding-left: 40px;
  padding-right: 40px;
  padding-top: 5px;
  padding-bottom: 5px;
}

.btnshowanswer{
  width: 130px;
  height: 35px;
  background: #FFFFFF 0% 0% no-repeat padding-box;
  border-radius: 5px;
  opacity: 1;
  border: 0px solid #1b1a1a;
  box-shadow: 0px 7px 6px #00000029;
  font-weight: bold;
  font-size: 14px;
  margin-right: 10px;
  color: #439803;
}

.feedbackinstruction {
  width: auto;
  height: auto;
  display: flex;
  flex-direction: row;
  padding: 10px 20px 10px 20px;
  background-color: rgb(255, 255, 255);
  border-radius: 5px;
  box-shadow: 0px 7px 6px #00000029;
  display: inline;
  text-align: left;

}

.blank{
  height: 30px;
  width: auto;
  border: 0px;
  background-color: rgb(238, 238, 238);
  
  font-weight: bold;
}
.tblcellincorrect{
  background-color: #F2B4B4;
  width: 115px;
  height: 30px;
  border-radius: 5px;
  box-shadow: inset 0px 3px 6px #00000029;
  padding-left: 10px;
  padding-right: 10px;
  border: 0px;
  margin: 8px;
}

.tblcellcorrect{
  background-color: #C0F2B4;
