  margin-left: 3px;
}


.space{
  width: 120px;
  height: 50px;
}


.center {
  border: 0px ;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 10px;
  display: flex;
  width: 50%;

}  

